import dynamic from 'next/dynamic';
import { useRouter } from "next/router";
import BreadCrumbs from "../../src/components/BreadCrumbs";
import { GameList } from "../../src/list/GameList";
import { Assets } from "../../src/list/Assets";
import { useEffect, useState, useRef } from "react";
import {Categories }from "../../src/list/PageList";
import GridLayout from '../../src/components/GridLayout.js';
import CategoryGames from '../../src/components/CategoryGames.js';
import Headcom from '../../src/components/Headcom.js';
import Ad_320x50 from '../../src/ads/Ad_320x50.js';

const FooterCom = dynamic(() => import('../../src/components/FooterCom'));
const FooterDis = dynamic(() => import('../../src/Description/FooterDis'));

const Snow_fx = dynamic(() => import('../../src/fx/Snow_fx'));
const CommenDis = dynamic(() => import('../../src/Description/CommenDis'));

const CatBtn = dynamic(() => import('../../src/components/CatBtn'));
const BlackScreen = dynamic(() => import('../../src/components/BlackScreen'));
const CookieCom = dynamic(() => import('../../src/components/CookieCom'));

const MobileExitButton = dynamic(() => import('../../src/components/MobileExitButton.js'));

const Ad_300x250_1 = dynamic(() => import('../../src/ads/Ad_300x250_1'), {
  ssr: false,
  loading: () => <p>Loading Ad...</p>,
});

const Ad_728x90_Bot = dynamic(() => import('../../src/ads/Ad_728x90_Bot'), {
  ssr: false,
  loading: () => <p>Loading Ad...</p>,
});

export async function getStaticPaths() {
  return {
    paths: GameList.map((page) => ({
      params: { id: page.name[1] },
    })),
    fallback: false,
  };
}

export async function getStaticProps({ params }) {
  const game = GameList.find(game => game.name[1] === params.id);
  return {
    props: {
      game, // only load this game’s data
    },
  };
}

